// Styling for the navbar
.navbar {
  opacity: 0.9;
  background-color: #fff;

  @media only screen and (min-width: 720px) {
    align-items: baseline;
    justify-content: center;
  }

  .navbar-brand {
    color: skyblue;
    font-size: 36px;
  }

  .navbar-toggler {
    height: 36px;
    width: 36px;
    padding: 6px 2px 0px 2px;
    margin-right: 20px;
    border: 1px solid black;

    span {
      top: 50%
    }
    span:before {
      content: '\2630';
    }
  }

  .nav-item {
    margin-right: 18px;
  }

  .nav-menu {
    max-width: 120px;
    display: flex;
    flex-flow: wrap;
    flex-direction: column;
  }

  a {
    color: gray;
    font-size: 12px;
  }

  img {
    height: 40px;
  }

  button {
    border: none;
    border-radius: 10px;
    background: none;
    padding: 0;
    margin: 0;
    padding-left: 4px;

    &:focus {
      outline: none;
      background-color: #eee;
      -webkit-transition: background-color 1000ms ease-in-out;
      -ms-transition: background-color 1000ms linear;
      transition: background-color 1000ms linear;
    }
  }
}